import React from 'react'
import {Route} from 'react-router-dom'
import './App.css'
import About from '../pages/about'
import Home from '../pages/home'


function App() {

    return (
        <div className="App">
            <Route exact path="/" component={Home}/>
            <Route exact path="/about" component={About}/>
        </div>
    )
}

export default App
